<template>
    <header id="header">
        <div class="left">
            <h1><a href="./index.html" title="JUST 쇼핑몰"><span>JUST</span> <span>쇼핑몰</span></a></h1>
        </div>
        <div class="right">
            <nav id="nav">
                <ul>
                    <li 
                        @mouseleave="onMouseOutNav"
                        class="col" 
                        v-for="(item,i) in 카테고리" 
                        :key="item.메인메뉴"                            
                    >
                        <a 
                            @mouseenter="onMouseOverMainBtn(i)"
                            href="#" 
                            :class="['main-btn', {'on':isOn[i]}]" 
                            :title="item.메인메뉴"
                        >
                            {{item.메인메뉴}}
