                            <div class="sub-container">
                                <ul>
                                    <li 
                                        v-for="(column,idx) in item.서브메뉴" 
                                        :key="idx"
                                    >
                                        <ul>
                                            <li 
                                                v-for="row in column"  
                                                :key="row"
                                            >
                                                <a href="#" :title="row">
                                                    {{row}}
                                                </a>
                                            </li>
                                        </ul>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </li>
                    
                </ul>

            </nav>
        </div>
