                    ['청바지','짧은바지','긴바지','레깅스'],
                    ['귀고리','목걸이','반지','팔찌']
                ]
            },
            {
                메인메뉴:'팬츠', 
                서브메뉴: [
                    ['블라우스','티','셔츠','니트'],
