    #header .right #nav > ul > li .sub .sub-container > ul > li > ul > li a {
        color:#999;
        display: flex;
        height: 30px;
        width: 100%;
        align-items: center;
        justify-content: center;
    }
    #header .right #nav > ul > li .sub .sub-container > ul > li > ul > li a:hover {
