<template>
    <section id="section1">
        <div class="slide-container">
            <div class="slide-content">
                <ul class="slide-wrap">
                    <li 
                        v-for="item,idx in 슬라이드"
                        :class="['slide', 'slide'+(idx+1)]"
                        :key="item"
                    >
                        <a href="#" :title="item">
                            <span>{{item}}</span>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
    </section>  
</template>

<script>
import $ from 'jquery';

export default {
    name: "Section1Component",
    props:[],
    data(){
        return{
            슬라이드 :[
                'JUST 쇼핑몰 오픈!',
                '종합 쇼핑의 새 막을 연다! JUST 쇼핑몰!',
                'JUST쇼핑몰 9월 세일 이벤트 안내'
            ]
        }
    },
    created(){
        
        const mainSlideFn=()=>{        
            let cnt=0;
            function mainSlide(){ 
                $('.slide').css({zIndex:1,opacity:1});
                $('.slide').eq(cnt)  .css({zIndex:2});
                $('.slide').eq( cnt-1 < 0 ? 2 : cnt-1 ).css({zIndex:3}).animate({opacity: 0}, 1000);
            }
            function mextCount(){
                cnt++;                
                if(cnt>2) cnt=0;
                mainSlide();
            }
            function autoTimer(){
                setInterval(mextCount, 3000);
            }
            autoTimer();
        }
        mainSlideFn();

    }

}
</script>

<style scoped>

    #section1 {width: 100%;height: 300px;background-color: #c9c;}
    /* 메인슬라이드 아래에서 위로이동 */
    #section1 .slide-container {width:100%;height: 100%;}
    #section1 .slide-container .slide-content {width: 100%;height: 100%;overflow: hidden;}
    #section1 .slide-container .slide-content .slide-wrap {
        width: 100%; 
        height: 100%;   
        position: relative;
    }
    #section1 .slide-container .slide-content .slide-wrap .slide {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url() no-repeat 50% 50%;
    }
    #section1 .slide-container .slide-content .slide-wrap .slide1 {
        background-image: url(./images/section1/banner_img.jpg);
        background-position: 50% 30%;
        background-size: 120% auto;
        z-index: 3;
    }
    #section1 .slide-container .slide-content .slide-wrap .slide2 {
        background-image: url(./images/section1/banner_img2.jpg);
        background-position: 50% 0%;
        background-size: 100% auto;
        z-index: 2;
    }
    #section1 .slide-container .slide-content .slide-wrap .slide3 {
        background-image: url(./images/section1/banner_img3.png);
        background-position: 50% 30%;
        background-size: 110% auto;
        z-index: 1;
    }

    #section1 .slide-container .slide-content .slide-wrap .slide  a {
        display: flex;
        width: 100%;
        height: 100%;
        align-items: center;
    }
    #section1 .slide-container .slide-content .slide-wrap .slide  a span {
        width: 100%;
        height: 70px;
        background: rgba(85, 170, 204, 0.7);
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 28px;
        color:#fff;
        user-select: none;
    }
    #section1 .slide-container .slide-content .slide-wrap .slide2 a span {background-color: rgba(204, 85, 170, 0.9);}
    #section1 .slide-container .slide-content .slide-wrap .slide3 a span {background-color: rgba(250, 120, 85, 0.9);}


</style>